<template>
    <div class="waterRoot">
        <div id='threeContainer'>

        </div>
    </div>
</template>
<script>
/**
 * 半成品，接下来处理地板贴图
 * 俯角透明，优先级高
 * 阴影、优先级低
 */
import * as THREE from 'three';
import { onMounted } from '@vue/runtime-core';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import {ShaderPass} from 'three/examples/jsm/postprocessing/ShaderPass.js'
import {RenderPass} from 'three/examples/jsm/postprocessing/RenderPass.js'
import { VertexNormalsHelper } from 'three/examples/jsm/helpers/VertexNormalsHelper.js';
const texture = new THREE.TextureLoader().load( 'textures/Material.001_baseColor.jpeg' )
const textureNormal = new THREE.TextureLoader().load( 'textures/normal.png' )
export default {
    name:'Water',
    mounted(){
        
            
            let composer,shaderPass,renderPass
            var dom = document.getElementById('threeContainer')
            const camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 0.01, 1000 );
            camera.position.z = 10.2;
            camera.position.y = 0.5;
            const scene = new THREE.Scene();
            const scene2 = new THREE.Scene();
            const scene3 = new THREE.Scene();
            
            const geometry = new THREE.PlaneGeometry( 10, 10,1,1 );
            // geometry.computeVertexNormals();
            // const material = new THREE.MeshNormalMaterial()
            
            const material = new THREE.ShaderMaterial({
                uniforms: {
                    'cameraNear': { value: camera.near },
                    'cameraFar': { value: camera.far },
                    'groundNTx':{value:textureNormal}
                },
                vertexShader: /* glsl */`
                varying vec2 vUv;
                varying vec3 v_position;
                varying vec3 v_normal;
                void main() {
                    vUv = uv;
                    v_normal = normalMatrix * normalize(normal);
                    gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );

                }`,

                fragmentShader: /* glsl */`
                #include <packing>
                varying vec2 vUv;
                varying vec3 v_normal;
                uniform float cameraNear;
                uniform float cameraFar;
                uniform sampler2D groundNTx;
                void main() {
                    vec3 nDetail = texture2D(groundNTx,vUv).rgb;
                    vec3 n = normalize(v_normal*nDetail);

                    // vec3 n = v_normal;
                    vec3 nv_color = n*0.5  + 0.5;
                    vec4 final2 = vec4(nv_color,1.);


                    gl_FragColor = final2;

                }`
            });
            const mesh = new THREE.Mesh( geometry, material );
            mesh.rotation.x = -Math.PI/2
            mesh.updateMatrix()
            scene2.add( mesh );

            const geometryForTexture = new THREE.PlaneGeometry( 10, 10,1,1 );
            const materialForTexture = new THREE.MeshPhongMaterial({map:texture})
            const meshForTexture = new THREE.Mesh(geometryForTexture,materialForTexture)
            meshForTexture.rotation.x = -Math.PI/2
            meshForTexture.material.map.wrapS = meshForTexture.material.map.wrapT = THREE.RepeatWrapping; 
            meshForTexture.material.map.matrix
                                    .identity() //矩阵重置
                                    .translate(0, 0 ) //设置中心点
                                    .rotate( 0 ) // 旋转
                                    .scale( 50, 50 ) //缩放
                                    .translate( 0, 0 ) //设置中心点
                                    .translate( 0, 0 ); 
            meshForTexture.material.map.needsUpdate = true;

            scene3.add( meshForTexture );

            
            // var vnh = new VertexNormalsHelper( mesh, 5 );
            // scene.add( vnh );

            const renderer = new THREE.WebGLRenderer( { antialias: true } );
            renderer.setSize( window.innerWidth, window.innerHeight );
            renderer.setAnimationLoop( animation );
            renderPass = new RenderPass(scene, camera);
            composer = new EffectComposer( renderer );
            composer.addPass(renderPass);

            var viewPortMatrixInverse = getViewPortMatrixInverse(
                    {x: 0, y: 0, width: window.innerWidth, height: window.innerHeight},
                    {near: 0, far: 1}
            );
            
            shaderPass = new ShaderPass({
                        glslVersion:THREE.GLSL3,
                        uniforms: {
                        'tDiffuse': { value: null },
                        'tDepth': { value: null },
                        'cameraNear': { value: camera.near },
                        'cameraFar': { value: camera.far },
                        'tNormal': {value:null},
                        'groundDepth': {value:null},
                        'viewPortMatrixInverse': { value: viewPortMatrixInverse},
                        'projectionMatrixInverse': {value: camera.projectionMatrixInverse},
                        'cameraProjectionMatrix':{value: camera.projectionMatrix},
                        'u_resolution':{value:new THREE.Vector2(window.innerWidth,window.innerHeight)},
                        'groundTexture':{ value:texture},
                        },
                        vertexShader: /* glsl */`
                        varying vec2 vUv;
                        varying vec3 v_position;
                        varying mat4 v_mvMatrix;
                        void main() {
                            vUv = uv;
                            v_mvMatrix = modelViewMatrix;
                            gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
        
                        }`,
        
                        fragmentShader: /* glsl */`
                        #include <packing>
                        varying vec2 vUv;
                        uniform float cameraNear;
                        uniform float cameraFar;
                        uniform sampler2D tDiffuse;
                        uniform sampler2D tDepth;
                        uniform sampler2D tNormal;
                        uniform sampler2D groundDepth;
                        uniform mat4 viewPortMatrixInverse;
                        uniform mat4 projectionMatrixInverse;
                        uniform mat4 cameraProjectionMatrix;
                        uniform vec2 u_resolution;
                        uniform sampler2D groundTexture;
                        float readDepth( sampler2D depthSampler, vec2 coord ) {
                            float fragCoordZ = texture2D( depthSampler, coord ).x;
                            float viewZ = perspectiveDepthToViewZ( fragCoordZ, cameraNear, cameraFar );
                            return viewZToOrthographicDepth( viewZ, cameraNear, cameraFar );
                        }
                        vec3 getViewNormal( const in vec2 uv ) {
                            return unpackRGBToNormal( texture2D( tNormal, uv ).xyz );
                        }
                        float getViewZ( const in float depth ) {
                            return perspectiveDepthToViewZ( depth, cameraNear, cameraFar );
                        }
                        vec4 getViewPosition( const in vec2 uv, const in float depth/*clip space*/, const in float clipW ) {
                            vec4 clipPosition = vec4( ( vec3( uv, depth ) - 0.5 ) * 2.0, 1.0 );//ndc
                            clipPosition *= clipW; //clip
                            return ( projectionMatrixInverse * clipPosition );//view
                        }
                        void main() {
                            float maxDistance = 2.;
                            float resolution  = 0.5;
                            float thickness   = .15;
                            
                            vec4 baseColor = texture2D(groundTexture,vUv);
                            vec4 color = texture2D(tDiffuse,vUv);
                            vec4 n = vec4(normalize(getViewNormal(vUv)),1.);
                            float depthMain = texture2D(tDepth,vUv).r;
                            float depthGround = texture2D(groundDepth,vUv).r;
        
                            vec2 groundUv;//叠上贴图，需要把地板视图转为uv
                            if(
                                // (n.r!=0. ||n.g!=0. ||n.b!=0.) && 
                                depthGround<depthMain){
                                //先获取片元到视角向量(eyePos)
                                float viewZ = getViewZ( depthGround );
                                float clipW = cameraProjectionMatrix[2][3] * viewZ+cameraProjectionMatrix[3][3];
                                vec4 viewPosition= getViewPosition( vUv, depthGround, clipW ); //地面视图空间坐标获取到了
                                
                                //反射方向
                                vec3 reflectDir = normalize(reflect(viewPosition.xyz,n.xyz));
                                //开始前进
                                color = vec4(1.);//texture2D(tDiffuse,vec2(reflectDir.y,reflectDir.x));
                                
        
                                vec4 startView = vec4(viewPosition.xyz + (reflectDir *           0.), 1.);
                                vec4 endView   = vec4(viewPosition.xyz + (reflectDir * maxDistance), 1.);
        
                                vec2 texSize  = u_resolution;
                                vec2 texCoord = gl_FragCoord.xy / texSize;
        
                                vec4 startFrag = cameraProjectionMatrix * startView;
                                startFrag.xyz/=startFrag.w;
                                startFrag.xy  = (startFrag.xy  + 1.)* 0.5;
                                startFrag.xy  *= texSize;
        
                                vec4 endFrag = cameraProjectionMatrix * endView;
                                endFrag.xyz/=endFrag.w;
                                endFrag.xy  = (endFrag.xy+1.) * 0.5;
                                endFrag.xy  *= texSize;
        
                                vec4 positionTo = viewPosition;
        
                                vec4 uv = vec4(0.0);
                                vec2 frag = startFrag.xy;
                                uv.xy = frag / texSize;
                                float deltaX    = endFrag.x - startFrag.x;
                                float deltaY    = endFrag.y - startFrag.y;
                                // 防止屏幕中间出现黑线
                                if(deltaX==0.){
                                    deltaX=1.;
                                }
                                if(deltaY==0.){
                                    deltaY=1.;
                                }
                                
                                float useX      = abs(deltaX) >= abs(deltaY) ? 1. : 0.;
                                float delta     = mix(abs(deltaY), abs(deltaX), useX) * clamp(resolution, 0., 1.);
                                vec2  increment = vec2(deltaX, deltaY) / max(delta, 0.001);
                                
                                //float curPositionX;
                                //float curPositionY;
                                // curPositionX = (startFrag.x)*(1.-search1)+(endFrag.x)*search1;
                                // curPositionY = (startFrag.y)*(1.-search1)+(endFrag.y)*search1;
        
        
                                float search0 = 0.;
                                float search1 = 0.;
                                
                                int hit0 = 0;
                                int hit1 = 0;
        
                                float viewDistance = startView.z;
                                float depth        = 0.;
        
                                float i = 0.;
                                for(i=0.;i<delta;i+=1.){
                                    if(delta>500.){
                                        gl_FragColor = baseColor;
                                        return;
                                    }
                                    frag      += increment;
                                    uv.xy      = frag / texSize;
                                    if(uv.x>1. || uv.y>1. || uv.x<0. || uv.y<0.){
                                        break;
                                    }
                                    
                                    float dd = texture2D(tDepth,uv.xy).r;
                                    float viewZZ = getViewZ( dd );
                                    float clipWW = cameraProjectionMatrix[2][3] * viewZZ+cameraProjectionMatrix[3][3];
                                    positionTo = vec4( getViewPosition( uv.xy, dd, clipWW ).xyz,1.); //地面视图空间坐标获取到了
                                   
                                    
                                    search1 = mix
                                        ( (frag.y - startFrag.y) / deltaY
                                        , (frag.x - startFrag.x) / deltaX
                                        , useX
                                        );
                                    search1 = clamp(search1, 0.0, 1.0);
                                    viewDistance = (startView.z * endView.z) / mix(endView.z, startView.z, search1);
                                    depth        = abs(positionTo.z)-abs(viewDistance);
                                    float wtfD = abs(viewDistance)-abs(positionTo.z);
                                    if (
                                        depth>wtfD &&
                                        depth < thickness 
                                        && dd!=1. ) 
                                    {
                                        hit0 = 1;
                                        break;
                                    }
                                }
        
                                if(hit0>0){
                                    color = texture2D(tDiffuse,uv.xy);
                                    color = mix(baseColor,color,0.5);
                                    //可以在这里模糊化反射
                                    
                                }else{
                                    color = baseColor;
                                }
                                
                            }
                            
                            gl_FragColor = color;
        
                        }`
                    });
            composer.addPass(shaderPass)

            const directionalLight = new THREE.DirectionalLight( 0xffffff, 1 );
            directionalLight.rotation.x = Math.PI/2
            directionalLight.rotation.z = Math.PI/2
            directionalLight.position.x = 50
            directionalLight.position.y = 10
            scene.add( directionalLight );
            const directionalLight2 = new THREE.DirectionalLight( 0xffffff,0.0 );
            directionalLight2.rotation.x = -Math.PI/2
            directionalLight2.rotation.z = -Math.PI/2
            scene3.add( directionalLight2 );

            const controls = new OrbitControls( camera, renderer.domElement );
            dom.appendChild( renderer.domElement );

            // skybox
            // const path = "textures/";
            // const format = '.png';
            // const urls = [
            //     path + 'px' + format, path + 'nx' + format,
            //     path + 'py' + format, path + 'ny' + format,
            //     path + 'pz' + format, path + 'nz' + format
            // ];

            // const textureCube = new THREE.CubeTextureLoader().load( urls );
            // scene.background = textureCube;

            var target = setupRenderTargetShadow()
            var targetN = setupRenderTargetNormal()
            var targetGroundTx = setupRenderTargetGroundTx()
            function animation( time ) {
                // camera.updateMatrix();
                composer.renderer.setRenderTarget( target );
                composer.renderer.render(scene,camera);
                shaderPass.uniforms.tDepth.value = target.depthTexture


                composer.renderer.setRenderTarget( targetN );
                composer.renderer.render(scene2,camera);
                shaderPass.uniforms.tNormal.value = targetN.texture
                shaderPass.uniforms.groundDepth.value = targetN.depthTexture
                

                composer.renderer.setRenderTarget( targetGroundTx );
                composer.renderer.render(scene3,camera);
                shaderPass.uniforms.groundTexture.value = targetGroundTx.texture
                

                composer.renderer.setRenderTarget( null );
                composer.render();
            }



            function setupRenderTargetShadow() {
                var target = new THREE.WebGLRenderTarget( window.innerWidth, window.innerHeight );
                target.texture.minFilter = THREE.NearestFilter;
                target.texture.magFilter = THREE.NearestFilter;
                target.stencilBuffer = ( THREE.DepthFormat === THREE.DepthStencilFormat ) ? true : false;
                target.depthTexture = new THREE.DepthTexture();
                target.depthTexture.format = THREE.DepthFormat;
                target.depthTexture.type = THREE.UnsignedShortType;
                return target
            }
            function setupRenderTargetNormal(){
                var normalRenderTarget = new THREE.WebGLRenderTarget( window.innerWidth, window.innerHeight, {
                    minFilter: THREE.NearestFilter,
                    magFilter: THREE.NearestFilter,
                    format: THREE.RGBAFormat,
                    type: THREE.HalfFloatType,
                } );
                
                normalRenderTarget.stencilBuffer = ( THREE.DepthFormat === THREE.DepthStencilFormat ) ? true : false;
                normalRenderTarget.depthTexture = new THREE.DepthTexture();
                normalRenderTarget.depthTexture.format = THREE.DepthFormat;
                normalRenderTarget.depthTexture.type = THREE.UnsignedShortType;

                normalRenderTarget.texture.mapping = THREE.UVMapping;
                return normalRenderTarget
            }
            function setupRenderTargetGroundTx(){
                var groundTxRenderTarget = new THREE.WebGLRenderTarget( window.innerWidth, window.innerHeight, {
                    minFilter: THREE.NearestFilter,
                    magFilter: THREE.NearestFilter,
                    format: THREE.RGBAFormat,
                    type: THREE.HalfFloatType,
                } );
                return groundTxRenderTarget
            }

            //被反射物
            new GLTFLoader()
					.setPath( 'car/' )
					.load( 'scene.gltf', function ( gltf ) {
                        scene.add(gltf.scene)
                        gltf.scene.scale.x = 0.01
                        gltf.scene.scale.y = 0.01
                        gltf.scene.scale.z = 0.01
                        gltf.scene.position.y = 0
                        // gltf.scene.position.z = -2
					} );


            function getViewPortMatrixInverse(viewPort, depthRange) {
                var _viewPortMatrix = new THREE.Matrix4();
                var _viewPortMatrixInverse = new THREE.Matrix4();
                _viewPortMatrix.set(
                        viewPort.width / 2, 0, 0, viewPort.x + viewPort.width / 2,
                        0, viewPort.height / 2, 0, viewPort.y + viewPort.height / 2,
                        0, 0, (depthRange.far - depthRange.near) / 2, (depthRange.far + depthRange.near) / 2,
                        0, 0, 0, 1
                );
                _viewPortMatrixInverse.getInverse(_viewPortMatrix, true);
                return _viewPortMatrixInverse;
            }
        
    }
}
</script>
<style lang='scss' scoped>
.waterRoot{
    height: 100%;
    background: rgb(116, 116, 116);
}
</style>